      <b-form-group>
        <div class="d-flex align-items-center">
          <label class="mr-1">Search</label>
          <b-form-input
            v-model="searchTerm"
            placeholder="Search"
            type="text"
            class="d-inline-block"
          />
        </div>
      </b-form-group>
      <b-form-group>
        <b-form-select
          v-model="selected"
          label="title"
          :options="filterOptions.filterDropdownItems"
        />
      </b-form-group>
      <b-form-group>
        <b-form-select
          v-model="EHRSystem"
          label="title"
          :options="filterOptions.filterDropdownItems"
        />
      </b-form-group>
    </div>

    <!-- table -->
  
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :rtl="direction"
      :search-options="{
        enabled: true,
        externalQuery: searchTerm }"
  
      :pagination-options="{
        enabled: true,
        perPage:pageLength
      }"
    >
      <template
        slot="table-row"
        slot-scope="props"
      >
        <div>


          
        <!-- Column: practice -->
        <span
          v-if="props.column.field === 'practice'"
          class="text-nowrap"
        >
          <span class="text-nowrap">{{ props.row.practice }}</span>
          <br>
          <span class="text-nowrap">{{ props.row.id }}</span>
        </span>

        <!-- Column: Address -->
        <span v-else-if="props.column.field === 'address'">
          <span>{{ props.row.address }}</span>
        </span>

        <!-- Column: Action -->
        <span v-else-if="props.column.field === 'action'">
          <span>
            <b-dropdown
              variant="link"
              toggle-class="text-decoration-none"
              no-caret
            >
              <template v-slot:button-content>
                <feather-icon
                  icon="MoreVerticalIcon"
                  size="16"
                  class="text-body align-middle mr-25"
                />
              </template>
              <b-dropdown-item>
                <feather-icon
                  icon="Edit2Icon"
                  class="mr-50"
                />
                <span>Edit</span>
              </b-dropdown-item>
              <b-dropdown-item>
                <feather-icon
                  icon="TrashIcon"
                  class="mr-50"
                />
                <span>Delete</span>
              </b-dropdown-item>
            </b-dropdown>
          </span>
        </span>

    <span v-else-if="props.column.field === 'status'">
              <b-badge :variant="statusVariant(props.row.status)">
                {{ props.row.status }}
              </b-badge>
            </span>

        <!-- Column: Common -->
        <span v-else>
          {{ props.formattedRow[props.column.field] }}
        </span>
        </div>
      </template>

      <!-- pagination -->
      <template
        slot="pagination-bottom"
        slot-scope="props"
      >
        <div class="d-flex justify-content-between flex-wrap">
          <div class="d-flex align-items-center mb-0 mt-1">
            <span class="text-nowrap ">
              Showing 1 to
            </span>
            <b-form-select
              v-model="pageLength"
              :options="['3','5','10']"
              class="mx-1"
              @input="(value)=>props.perPageChanged({currentPerPage:value})"
            />
            <span class="text-nowrap"> of {{ props.total }} entries </span>
          </div>
          <div>
            <b-pagination
              :value="1"
              :total-rows="props.total"
              :per-page="pageLength"
              first-number
              last-number
              align="right"
              prev-class="prev-item"
              next-class="next-item"
              class="mt-1 mb-0"
              @input="(value)=>props.pageChanged({currentPage:value})"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </div>
        </div>
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import 'vue-good-table/dist/vue-good-table.css'
import {
  BAvatar, BBadge, BPagination, BFormGroup, BFormInput, BFormSelect, BDropdown, BDropdownItem,
} from 'bootstrap-vue'
import { VueGoodTable } from 'vue-good-table'
import store from '@/store/index'

export default {
  components: {
    VueGoodTable,
    BAvatar,
    BBadge,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
  },
  data() {
    return {
      pageLength: 5,
      dir: false,
      columns: [
          {
          label: 'PRACTICE',
          field: 'practice',
           filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
              enabled: true, // enable filter for this column
              filterValue: null, // initial populated value for this filter
              trigger: 'enter', //only trigger on enter not on keyup 
              filterFn: this.columnFilterFn,
            },
        },
        {
          label: 'ADDRESS',
          field: 'address',
        },
        {
          label: 'CONTACT INFO',
          field: 'contact',
        },
        {
          label: 'STATUS',
          field: 'status',
          filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
              enabled: true, // enable filter for this column
              filterValue: null, // initial populated value for this filter
              trigger: 'enter', //only trigger on enter not on keyup 
              filterFn: this.columnFilterFn, //custom filter function that
            },
        },
        {
          label: 'ACTIONS',
          field: 'score',
        
        },
        
      ],
       filterOptions: {
            filterDropdownItems: ["Active","Inactive"], // dropdown (with selected values) instead of text input
          },
      rows: [
        { 
          id:'ID: 94723',
          practice:"Family & Wellness Medicine, LLC",
          address: '34004 16th Ave. S., Ste. 100 Federal Way, WA 98003', 
          contact: 'wsomerlie1l@accuweather.com Phone:  253-944-1223 Fax: 253-944-1255',
          status: "Active",
          score: 'View' 
        },
        { 
          id:'ID: 35471',
          practice:"Focus Health DPC",
          address: '2522 N. Proctor St., #228 Tacoma, WA 98406', 
          contact: 'Phone: 206-372-3194',
          status: "Active",
          score: 'View' 
        },
        { id:3, practice:"Susan", address: "16", contact: '2011-10-30', status: "Applied",score: 0.03343 },
        { id:4, practice:"Chris", address: "55", contact: '2011-10-11', status: "Inactive",score: 0.03343 },
        { id:5, practice:"Dan", address: "40", contact: '2011-10-21', status: "Professional",score: 0.03343 },
        { id:6, practice:"John", address: "20", contact: '2011-10-31', status: "Active",score: 0.03343 },],
      searchTerm: '',
      selected:null
    }
  },
  computed: {
    statusVariant() {
      const statusColor = {
        /* eslint-disable key-spacing */
        Active       : 'light-success',
        Inactive     : 'light-dark',
        Professional : 'light-primary',
        Rejected     : 'light-danger',
        Resigned     : 'light-warning',
        Applied      : 'light-info',
        /* eslint-enable key-spacing */
      }

      return status => statusColor[status]
    },
    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true
        return this.dir
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false
      return this.dir
    },
  },
  watch:{
    selected(){
        let found = this.columns.find((c) => {
          return c.field == 'status'
        });
        found.filterOptions.filterValue = this.selected;
    }
  },
  created() {
    // this.$http.get('/good-table/basic')
    //   .then(res => { this.rows = res.data })
  },
}
</script>